.cart-dropdown {
    position: absolute;
    width: 240px;
    height: 340px;
    display: flex;
    flex-direction: column;
    padding: 20px;
    border: 1px solid black;
    background-color: white;
    top: 90px;
    right: 65px;
    z-index: 5;
  
}
.cart-dropdown .empty-message{
  font-size: 18px;
  margin: 50px auto;
}
.cart-dropdown .cart-items {
  height: 240px;
  display: flex;
  flex-direction: column;
  overflow: scroll;
}
.cart-dropdown button {
    margin-top: auto;
}